.heading {
  font-size: 48px;
  line-height: 1;
  position: absolute;
  text-transform: uppercase;
  font-weight: 300;
  width: max-content;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    top: calc(100% + 4px);
    height: 2px;
    width: calc(50% - 20px);

    background-color: $highlight-color;
  }

  &::before {
    left: 0;
  }

  &::after {
    right: 0;
  }

  &__crown {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 4px);
    width: 20px;
    height: 13px;
    margin: auto;

    background-image: url(../images/crown.svg);
  }
}

@media screen and (max-width: $mobile-breakpoint) {
  .heading {
    font-size: 40px;
  }
}
