@import "variables";

html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3 {
  margin: 0;
  border-width: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  font-style: 16px;
  line-height: inherit;
}

body {
  line-height: 1.5;
}

strong {
  font-weight: 500;
}

a {
  color: $link-color;
  text-decoration: none;
}
